import React, {useEffect, useState} from 'react'
import { useParams } from 'react-router-dom'
import Sidebar from '../../../components/sidebar/Sidebar'
import TopBar from '../../../components/topBar/TopBar'
import axios from 'axios'
import './viewbook.css';
import { Card } from '@material-ui/core'
import { endpoint } from '../../../assets/appconst'

export default function ViewBook() {
    let {id} = useParams();
    const [livre, setLivre] = useState({})
    const getLivre = async () => {
       await axios.get(`${endpoint}/api/livre/${id}`)
       .then((data)=>{
            setLivre(data.data)
       })
    }
    useEffect(() => {
        getLivre();
    }, [])
    console.log(livre);
    return (
        <>
        <TopBar/>
        <div className='viewoffres'>
            <div className='viewoffres1'>
                <Sidebar/>
            </div>
            <div className='viewoffres2'>
                <Card elevation={3} style={{padding: 20, justifyItems: "center"}}>
                    <div className="card mb-3" style={{maxWidth: 740, border: 'none'}}>
                        <div className="row g-0">
                            <div className="col-md-4">
                                <Card elevation={2} style={{padding: 2}}>
                                    <img style={{borderRadius: 5}} src={`${endpoint}/images/${livre.couverture}`} className="img-fluid rounded-start" alt={livre.couverture}/>
                                </Card>
                            </div>
                            <div className="col-md-8">
                            <div className="card-body">
                                <h5 className="card-title">{livre.Titre}</h5>
                                <p className="card-text"> {livre.desc} </p>
                            </div>
                            </div>
                        </div>
                    </div>
                </Card>
            </div>
            
        </div>
        </>
    )
}
