import React, { Component } from 'react';
import "./sidebar.css";
import {LineStyle,RssFeed,
  Chat,
  Group,
  Bookmark,
  WorkOutline,
  Event,} from "@material-ui/icons";
import {Link, NavLink} from "react-router-dom";


const menuItems = ['Home','Anciens','Offres','Livres','Evenements'];

class Sidebar extends Component {
    constructor(props) {
        super(props);
        this.state = { menuItems : [] };
      }
    _handleClick(menuItem) { 
        this.setState({ active: menuItem });
      }
    render() {
        const activeStyle = { color: "#304CA6" };
        return (
            <div className='sidebar'>
                <div className="sidebareWrapper">
                    <div className="sidebareMenu">
                        <ul className="sidebarList">
                            {menuItems.map(menuItem => 
                                <Link to={menuItem} className='lien'
                                style={this.state.active === menuItem ? activeStyle : {}}
                                onClick={this._handleClick.bind(this, menuItem)}
                                > 
                                <li className="sidebarItem">
                                    {
                                        menuItem==="Home" && <div><LineStyle className="sidebarIcon" /> Home</div>
                                        || menuItem==="Anciens" && <div><Group className="sidebarIcon" />Anciens</div>
                                        || menuItem==="Offres" && <div><WorkOutline className="sidebarIcon" />Offres</div>
                                        || menuItem==="Livres" && <div><Bookmark className="sidebarIcon" />Livres</div>
                                        || menuItem==="Evenements" && <div><Event className="sidebarIcon" />Evenements </div>
                                    }
                                </li>
                                </Link>
                            )}
                        </ul>
                    </div>
                </div>           
            </div>
        );
    }
}


export default Sidebar;