import React, {useEffect, useState} from 'react'
import { Link, useParams } from 'react-router-dom'
import Sidebar from '../../../components/sidebar/Sidebar'
import TopBar from '../../../components/topBar/TopBar'
import axios from 'axios'
import { GetApp } from '@material-ui/icons'
import './viewoffre.css';
import { Button } from '@material-ui/core'
import { endpoint } from '../../../assets/appconst'

export default function ViewOffre() {
    let {id} = useParams();
    const [offre, setOffre] = useState({})
    const getEvent = async () => {
       await axios.get(`${endpoint}/api/offre/${id}`)
       .then((data)=>{
            setOffre(data.data)
       })
    }
    useEffect(() => {
        getEvent();
    }, [])
    console.log(offre);
    return (
        <>
        <TopBar/>
        <div className='viewoffres'>
            <div className='viewoffres1'>
                <Sidebar/>
            </div>
            <div className='viewoffres2'>
                <div className="card" style={{borderLeft: "none", borderRight: "none", borderBottom: "2px solid darkblue", borderRadius: 0}}>
                    <div className="card-header" style={{backgroundColor:"darkblue", color: "white", letterSpacing: 5}}>
                        <h3>Institution : <strong style={{textTransform: 'uppercase', letterSpacing: 5}}>{offre.organisation}</strong></h3>
                    </div>
                    <div className="card-body">
                        <h4 className="card-title"> Post : <strong style={{textTransform: 'uppercase'}}> {offre.Titre}</strong></h4>
                        <h6 className="card-subtitle mb-2 text-muted"> <strong>Publiée : </strong> {new Date(offre.datePublication).toLocaleDateString()}</h6>
                        <h4 className='mb-2'>Description de l'offre</h4>
                        <p class="card-text">{offre.desc}</p>
                    </div>
                    <div className="card-footer" style={{backgroundColor: "white"}}>
                        <h3>Exipire en date du : <strong style={{color: "darkblue", fontSize: 20, letterSpacing: 10, paddingRight: 380}}>{new Date(offre.dateExpiration).toLocaleDateString()}</strong> <a href={`http://localhost:8800/images/${offre.pdfLink}`} style={{textDecoration:"none", color:"inherit"}}><Button variant="contained"
                        color="primary"
                        size="small"
                        startIcon={<GetApp/>}>Télécharger</Button></a> </h3>
                    </div>
                </div>
            </div>
            
        </div>
        </>
    )
}
