import 'date-fns';
import React, {useRef, useState} from 'react';
import './insertlivre.css'
import Sidebar from '../../../components/sidebar/Sidebar'
import TopBar from '../../../components/topBar/TopBar'
import { Paper, Button } from '@material-ui/core';
import {Save} from "@material-ui/icons";
import axios from "axios";
import { endpoint } from '../../../assets/appconst';

export default function InsertLivre() {
    const desc = useRef();
    const Titre = useRef();
    const [file, setFile] = useState(null);

  const submitHandler = async (e) => {
    e.preventDefault();
    const newPost = {
        Titre: Titre.current.value,
        desc: desc.current.value
    };
    if (file) {
      const data = new FormData();
      const fileName = Date.now() + file.name;
      data.append("name", fileName);
      data.append("file", file);
      newPost.couverture = fileName;
      console.log(newPost);
      try {
        await axios.post(`${endpoint}/api/livre/upload`, data);
      } catch (err) {}
    }
    try {
      await axios.post(`${endpoint}/api/livre`, newPost);
      window.location.reload();
    } catch (err) {}
  };

    return (
        <>
            <TopBar/>
            <div className='insertoffre'>
                <div className='insertoffre1'>
                    <Sidebar/>
                </div>
                <form onSubmit={submitHandler} className='insertoffre2'>
                <h2>Livre/ Insertion</h2>
                <Paper elevation={3} className="paper">
                    <div class="mb-3">
                        <label for="exampleInputEmail1" class="form-label">Titre du livre</label>
                        <input ref={Titre} type="text" class="form-control" id="exampleInputEmail1" placeholder='Organisation'/>
                    </div>
                    <div class="mb-3">
                        <label for="floatingTextarea">Description du livre</label>
                        <textarea ref={desc} class="form-control" placeholder="Description de l'offre" id="floatingTextarea"></textarea>
                    </div>
                    <div class="mb-3">
                        <label for="exampleInputPassword1" class="form-label">Couverture du livre</label>
                        <input onChange={(e) => setFile(e.target.files[0])} type="file" class="form-control" id="exampleInputPassword1"/>
                    </div>
                    <Button style={{
                            backgroundColor:"#304CA6",
                            color:"#FFF",
                            marginTop: "20px"
                        }} type="submit" variant="contained" startIcon={<Save/>}>Sauvegarder</Button>
                </Paper>
                </form>
            </div>
        </>
    )
}
