import React, {useState, useEffect} from 'react';
import "./featuredInfo.css";
import axios from 'axios';
import {Link} from "react-router-dom"

const FeaturedInfo=()=>{
    const [countUser, setCountUser] = useState(0)
    const [countOffre, setCountOffre] = useState(0)
    const [countBook, setCountBook] = useState(0)
    const getCountUser = async()=>{
        await axios.get("http://localhost:8800/api/user/count")
        .then((count)=>{
            setCountUser(count.data)
        })
    }
    const getBook = async()=>{
        await axios.get("http://localhost:8800/api/livre/count/livres")
        .then((count)=>{
            setCountBook(count.data)
        })
    }
    const getCountOffre = async()=>{
        await axios.get("http://localhost:8800/api/offre/count/offres")
        .then((count)=>{
            setCountOffre(count.data)
        })
    }
    useEffect(() => {
        getCountUser();
        getBook();
        getCountOffre();
    }, [])
        return (
            <div className="featured">
                <div className="featuredItem">
                    <Link to="/anciens" style={{ textDecoration: "none", color:"black" }}>
                        <span className="featuredTitle">Anciens</span>
                        <div className="batimentTotal">
                            <span className="nombrebatiment"> {countUser >1 ? countUser + " users" : countUser + " user"}</span>
                        </div>
                    </Link>
                </div>
                <div className="featuredItem">
                    <Link to="/livres" style={{ textDecoration: "none", color:"black" }}>
                        <span className="featuredTitle">Nos Livres</span>
                        <div className="batimentTotal">
                            <span className="nombrebatiment"> {countBook >1 ? countBook + " livres" : countBook + " livre"}</span>
                        </div>
                    </Link>
                </div>
                <div className="featuredItem">
                    <Link to="/offres" style={{ textDecoration: "none", color:"black" }}>
                        <span className="featuredTitle">Offres d'emplois</span>
                        <div className="batimentTotal">
                            <span className="nombrebatiment"> {countOffre >1 ? countOffre + " offres" : countOffre + " offre"}</span>
                        </div>
                    </Link>
                </div>
            </div>
        );
    }

export default FeaturedInfo;