import React, {useEffect, useState} from 'react';
import MaterialTable from 'material-table';
import { Link } from 'react-router-dom';
import {Visibility} from '@material-ui/icons';
import axios from 'axios'
import { Card } from '@material-ui/core';
function TableCustomers() {
   const [data, setData]=useState([])
   const deleteCustomer = async (olData, resolve)=>{
    await axios.delete(`http://localhost:8800/api/user/${olData._id}`)
    .then(res => {
        getAllUsers()
        resolve();
    })}
    const getAllUsers= async ()=>{
        const res= await axios.get(`http://localhost:8800/api/user/find`)
        console.log(res.data)
        setData(res.data)
    }
    const editCustomer = async (newRow, oldRow) => {
        await axios.put(`http://localhost:8800/api/user/edit/${oldRow._id}`, newRow)
        .then(res=>{
            getAllUsers()
        })
    }
    const addUser = async(newData, resolve)=>{
      axios.post("http://localhost:8800/api/user/register", newData)
      .then(res=>{
        let dataToAdd = [...data];
            dataToAdd.push(newData);
            setData(dataToAdd);
            resolve()
      })
    }
    useEffect(()=>{
        getAllUsers()
    },[])
  return (
    <Card elevation={1} style={{padding: 20, margin: 20}}>
      <MaterialTable
        title="Liste d'anciens étudiants / ULPGL"
        columns={[
          {title: "#", field: "tableData.id", editable: false, sorting: false, type:"numeric", filtering:false},
          {
            title: 'Utilisateur', field: 'username',
          },
          { title: 'Matricule', field: 'email' },
          { title: 'Ville', field: 'city', sorting: false },
          { title: 'Origine' , field: 'from'},
          { title: 'Date', field:'createdAt', type:'date'}
        ]}
        data={data}
        options={{
          headerStyle: {
            backgroundColor: '#304CA6',
            color: '#FFF'
          },
          exportButton: true,
          filtering: true,
          grouping: true,
          sorting: true,
          actionsColumnIndex: -1,
          pageSizeOptions: [2, 5, 10, 15, 20, 25, 50, 100, 500],
          paginationType: "stepped",
        }}
        editable={{
          onRowDelete: (olData)=>
                new Promise((resolve)=>
                {
                  deleteCustomer(olData, resolve)
                } )
        }}
        actions={[
          rowData=>(
            {
              icon:()=><Link to={`/viewuser/${rowData._id}`} style={{textDecoration:"none", color:"black"}}><Visibility/></Link>,
              tooltip: 'view',
              onClick: (rowData)
            }
          )
        ]}
      />
    </Card>
  )
}

export default TableCustomers