import React, {useState, useEffect} from 'react';
import {Link} from 'react-router-dom';
import {Container} from 'react-bootstrap';
import { Button } from '@material-ui/core';
import MaterialTable from 'material-table'
import Sidebar from '../../components/sidebar/Sidebar';
import TopBar from '../../components/topBar/TopBar';
import { VisibilityOutlined, Add } from '@material-ui/icons';

import axios from 'axios';
import { endpoint } from '../../assets/appconst';
const Livres = () => {
    const [data, setData]=useState([])
    const deleteBook = async (olData, resolve)=>{
     await axios.delete(`${endpoint}/api/livre/${olData._id}`)
     .then(res => {
         getAllBooks()
         resolve();
     })}
     const getAllBooks= async ()=>{
         const res= await axios.get(`${endpoint}/api/livre`)
         console.log(res.data)
         setData(res.data)
     }
     const editBook = async (newRow, oldRow) => {
         await axios.put(`${endpoint}/api/livre/${oldRow._id}`, newRow)
         .then(res=>{
             getAllBooks()
         })
     }
     useEffect(() => {
      getAllBooks()
     }, [])
    return (
        <>
        <TopBar/>
        <div className="home">
            <div className="home-1">
                <Sidebar/>
            </div>
            <div className="home-2">
                    <div class="d-grid gap-2 d-md-block">
                        <div class="btn-group" role="group" >
                            <Link to="/insertlivre" style={{textDecoration: "none", color:"inherit"}}>
                                <Button type="button" className='btn' style={{
                                backgroundColor:"#304CA6",
                                color:"#FFF",
                                marginBottom: "20px"
                                }}
                                startIcon={<Add/>}
                                >Nouveau Livre</Button>
                            </Link>
                            
                        </div>
                    </div>
                <Container>
                <MaterialTable
                    title="Livres"
                    columns={[
                      { title: 'Couverture', field: 'image', filtering: false, editable:false,
                      render: rowData => <img src={`${endpoint}/images/${rowData.couverture}`}
                       style={{width: 40, borderRadius: '50%'}} alt={rowData.image}/>  },
                        { title: 'Titre', field: 'Titre' },
                        { title: 'Description', field: 'desc' },
                        
                    ]}
                    data={data}
                    options={{
                        headerStyle: {
                          backgroundColor: '#304CA6',
                          color: '#FFF'
                        },
                        exportButton: true,
                        filtering: true,
                        grouping: true,
                        sorting: true,
                        actionsColumnIndex: -1,
                        pageSizeOptions: [2, 5, 10, 15, 20, 25, 50, 100, 500, 1000, 5000, 10000]
                      }}
                      editable={{
                        onRowDelete: (olData)=>
                              new Promise((resolve)=>
                              {
                                deleteBook(olData, resolve)
                              } ),
                        onRowUpdate: (newRow, oldRow)=> new Promise((resolve, reject)=>{
                          editBook(newRow, oldRow);
                          setTimeout(()=>resolve(), 500)
                        })
                      }}
                      actions={[
                        rowData=>(
                          {
                            icon:()=><Link to={`/viewbook/${rowData._id}`} style={{textDecoration:"none", color:"black"}}><VisibilityOutlined/></Link>,
                            tooltip: 'view',
                            onClick: (rowData)
                          }
                        )
                      ]}
                    />
                </Container>
            </div>
    </div>
    </>
    );
}

export default Livres;
