import React, {useState, useEffect} from 'react';
import {Link} from 'react-router-dom';
import {Container} from 'react-bootstrap';
import { Button } from '@material-ui/core';
import MaterialTable from 'material-table'
import Sidebar from '../../components/sidebar/Sidebar';
import TopBar from '../../components/topBar/TopBar';
import { DeleteOutline, Edit, VisibilityOutlined, Add } from '@material-ui/icons';
import axios from 'axios';
import { endpoint } from '../../assets/appconst';
const Evenements = () => {

    const [data, setData]=useState([])

    const deleteEvent = async (olData, resolve)=>{
     await axios.delete(`${endpoint}/api/evenements/${olData._id}`)
     .then(res => {
      getEvenements()
         resolve();
     })}
     const getEvenements= async ()=>{
         const res= await axios.get(`${endpoint}/api/evenements`)
         setData(res.data)
     }
     const editEvent = async (newRow, oldRow) => {
         await axios.put(`${endpoint}/api/evenements/${oldRow._id}`, newRow)
         .then(res=>{
          getEvenements()
         })
     }
     useEffect(() => {
      getEvenements()
     }, [])
    return (
        <>
        <TopBar/>
        <div className="home">
            <div className="home-1">
                <Sidebar/>
            </div>
            <div className="home-2">
                    <div class="d-grid gap-2 d-md-block">
                        <div class="btn-group" role="group" >
                            <Link to="/insertevenement" style={{textDecoration: "none", color:"inherit"}}>
                                <Button type="button" className='btn' style={{
                                backgroundColor:"#304CA6",
                                color:"#FFF",
                                marginBottom: "20px"
                                }}
                                startIcon={<Add/>}
                                >Nouveau Evenement</Button>
                            </Link>
                            
                        </div>
                    </div>
                <Container>
                <MaterialTable
                    title="Evenements"
                    columns={[
                        { title: 'Couverture', field: 'image', filtering: false, editable:false,
                        render: rowData => <img src={`${endpoint}/images/${rowData.image}`}
                         style={{width: 40, borderRadius: '50%'}}/>  },
                        { title: 'Titre', field: 'Titre' },
                        { title: 'Description', field: 'desc' },
                        {
                        title: 'Date',
                        field: 'dateEvenement',
                        type: 'datetime'
                        },
                    ]}
                    data={data}
                    options={{
                        headerStyle: {
                          backgroundColor: '#304CA6',
                          color: '#FFF'
                        },
                        exportButton: true,
                        filtering: true,
                        grouping: true,
                        sorting: true,
                        actionsColumnIndex: -1,
                        pageSizeOptions: [5, 10, 15, 20, 25, 50, 100, 500, 1000, 5000, 10000]
                      }}
                      editable={{
                        onRowDelete: (olData)=>
                              new Promise((resolve)=>
                              {
                                deleteEvent(olData, resolve)
                              } ),
                        onRowUpdate: (newRow, oldRow)=> new Promise((resolve, reject)=>{
                          editEvent(newRow, oldRow);
                          setTimeout(()=>resolve(), 500)
                        })
                      }}
                      actions={[
                        rowData=>(
                          {
                            icon:()=><Link to={`/viewevent/${rowData._id}`} style={{textDecoration:"none", color:"inherit"}}><VisibilityOutlined style={{color:"#304CA6"}} /></Link>,
                            tooltip: 'view',
                            onClick: (rowData)
                          }
                        )
                      ]}
                    />
                </Container>
            </div>
    </div>
    </>
    );
}

export default Evenements;
