import "./app.css";
import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import Home from "./pages/home/Home";
import {BrowserRouter as Router, Switch, Route} from "react-router-dom";
import Login from "./pages/users/Login";
import NotFound from "./pages/erreur/NotFound";
import Anciens from "./pages/anciens/Anciens";
import Offres from "./pages/offres/Offres";
import Livres from './pages/livres/Livres';
import Evenements from "./pages/evenements/Evenements";
import Communiques from "./pages/communiques/Communiques";
import InsertOffre from "./pages/offres/inserts/InsertOffre";
import InsertEvements from "./pages/evenements/inserts/InsertEvenements";
import InsertLivre from "./pages/livres/inserts/InsertLivre";
import ViewEvent from "./pages/evenements/view/ViewEvent";
import ViewOffre from "./pages/offres/viewoffre/ViewOffre";
import ViewBook from "./pages/livres/viewbook/ViewBook";

function App() {


  return (
    <Router>



    <div className="App">

      <div className="containerDiv">

        <Switch>
          <Route exact path="/" component={Login}/>
          <Route exact path="/home" component={Home}/>
          <Route exact path="/anciens" component={Anciens}/>
          <Route exact path="/offres" component={Offres}/>
          <Route exact path="/livres" component={Livres}/>
          <Route exact path="/evenements" component={Evenements}/>
          <Route exact path="/communiques" component={Communiques}/>
          <Route exact path="/insertoffre" component={InsertOffre}/>
          <Route exact path="/insertevenement" component={InsertEvements}/>
          <Route exact path="/insertlivre" component={InsertLivre}/>
          <Route exact path="/viewevent/:id" component={ViewEvent}/>
          <Route exact path="/viewoffre/:id" component={ViewOffre}/>
          <Route exact path="/viewbook/:id" component={ViewBook}/>
          <Route component={NotFound} />
        </Switch>


      </div>

    </div>

    </Router>
  );
}

export default App;
