import 'date-fns';
import React, {useRef, useState} from 'react';
import DateFnsUtils from '@date-io/date-fns';
import './insertevenements.css'
import Sidebar from '../../../components/sidebar/Sidebar'
import TopBar from '../../../components/topBar/TopBar'
import {
    MuiPickersUtilsProvider,
    KeyboardDatePicker,
  } from '@material-ui/pickers';
import { Paper, Button, Grid } from '@material-ui/core';
import { Save } from '@material-ui/icons';
import axios from 'axios';
import { endpoint } from '../../../assets/appconst';

export default function InsertEvements() {
    const [selectedDate, setSelectedDate] = React.useState(new Date());
    const desc = useRef();
    const Titre = useRef();
    const dateEvenement = useRef();
    const [file, setFile] = useState(null);

  const submitHandler = async (e) => {
    e.preventDefault();
    const newPost = {
        Titre: Titre.current.value,
        desc: desc.current.value,
        dateEvenement: selectedDate
    };
    if (file) {
      const data = new FormData();
      const fileName = Date.now() + file.name;
      data.append("name", fileName);
      data.append("file", file);
      newPost.image = fileName;
      console.log(newPost);
      try {
        await axios.post(`${endpoint}/api/event/upload`, data);
      } catch (err) {}
    }
    try {
      await axios.post(`${endpoint}/api/evenements`, newPost);
      window.location.reload();
    } catch (err) {}
  };

  const handleDateChange = (date) => {
    setSelectedDate(date);
  };
    return (
        <>
            <TopBar/>
            <div className='insertoffre'>
                <div className='insertoffre1'>
                    <Sidebar/>
                </div>
                <div className='insertoffre2'>
                <h2>Evenement/ Insertion</h2>
                <Paper elevation={3} className="paper">
                    <form onSubmit={submitHandler}>
                        <div class="mb-3">
                            <label htmlFor="exampleInputEmail1" class="form-label">Titre d'évenement</label>
                            <input ref={Titre} type="text" class="form-control" id="exampleInputEmail1" placeholder='Organisation'/>
                        </div>
                        <div class="mb-3">
                            <label htmlFor="floatingTextarea">Description de l'évenement</label>
                            <textarea ref={desc} class="form-control" placeholder="Description de l'évenement" id="floatingTextarea"></textarea>
                        </div>
                        <div class="mb-3">
                            <label htmlFor="image" className="form-label">Resource d'évenement</label>
                            <input onChange={(e) => setFile(e.target.files[0])} type="file" class="form-control" id="image"/>
                        </div>
                        <MuiPickersUtilsProvider utils={DateFnsUtils}>
                            <Grid container justifyContent="space-around">
                                <KeyboardDatePicker
                                fullWidth
                                margin="normal"
                                id="date-picker-dialog"
                                label="Date Evenement"
                                format="MM/dd/yyyy"
                                value={selectedDate}
                                onChange={handleDateChange}
                                KeyboardButtonProps={{
                                    'aria-label': 'change date',
                                }}
                                />
                            </Grid>
                        </MuiPickersUtilsProvider>
                        <Button style={{
                            backgroundColor:"#304CA6",
                            color:"#FFF",
                            marginTop: "20px"
                        }} 
                        type="submit" 
                        variant="contained"
                        startIcon={<Save/>}
                        >Sauvegarder</Button>
                    </form>
                </Paper>
                </div>
            </div>
        </>
    )
}
