import 'date-fns';
import React, {useState, useRef} from 'react';
import Grid from '@material-ui/core/Grid';
import DateFnsUtils from '@date-io/date-fns';
import './insertoffre.css'
import Sidebar from '../../../components/sidebar/Sidebar'
import TopBar from '../../../components/topBar/TopBar'
import {
    MuiPickersUtilsProvider,
    KeyboardDatePicker,
  } from '@material-ui/pickers';
import { Paper, Button } from '@material-ui/core';
import axios from "axios"
import { endpoint } from '../../../assets/appconst';

export default function InsertOffre() {
    const [selectedDate, setSelectedDate] = useState(new Date());
    const [selectedDate1, setSelectedDate1] = useState(new Date());
    const [file, setFile] = useState(null);
    const desc = useRef();
    const Titre = useRef();
    const organisation = useRef();


    const handleDateChange1 = (date) => {
        setSelectedDate1(date);
    };

    const handleDateChange = (date) => {
        setSelectedDate(date);
    };

    

  const submitHandler = async (e) => {
    e.preventDefault();
    const newOffre = {
        Titre: Titre.current.value,
        desc: desc.current.value,
        organisation: organisation.current.value,
        datePublication: selectedDate1,
        dateExpiration: selectedDate
    };
    if (file) {
      const data = new FormData();
      const fileName = Date.now() + file.name;
      data.append("name", fileName);
      data.append("file", file);
      newOffre.pdfLink = fileName;
      console.log(newOffre);
      try {
        await axios.post(`${endpoint}/api/livre/upload`, data);
      } catch (err) {}
    }
    try {
      await axios.post(`${endpoint}/api/offre/save`, newOffre);
      window.location.reload();
    } catch (err) {}
  };
    return (
        <>
            <TopBar/>
            <div className='insertoffre'>
                <div className='insertoffre1'>
                    <Sidebar/>
                </div>
                <form className='insertoffre2' onSubmit={submitHandler}>
                <h2>Offre d'emploi/ Insertion</h2>
                <Paper elevation={3} className="paper">
                    <div className="mb-2">
                        <label for="exampleInputEmail1" className="form-label">Organisation</label>
                        <input ref={organisation} type="text" className="form-control" id="exampleInputEmail1" placeholder='Organisation'/>
                    </div>
                    <div className="mb-2">
                        <label for="exampleInputPassword1" className="form-label">Titre</label>
                        <input type="text" ref={Titre} className="form-control" id="exampleInputPassword1"/>
                    </div>
                    <div className="mb-2">
                        <label for="floatingTextarea">Description</label>
                        <textarea className="form-control" ref={desc} placeholder="Description de l'offre" id="floatingTextarea"></textarea>
                    </div>
                    <MuiPickersUtilsProvider utils={DateFnsUtils}>
                        <Grid container justifyContent="space-around">
                            <KeyboardDatePicker
                            fullWidth
                            margin="normal"
                            id="date-picker-dialog"
                            label="Date de publication"
                            format="MM/dd/yyyy"
                            value={selectedDate1}
                            onChange={handleDateChange1}
                            KeyboardButtonProps={{
                                'aria-label': 'change date',
                            }}
                            />
                        </Grid>
                        <Grid container justifyContent="space-around">
                            <KeyboardDatePicker
                            fullWidth
                            margin="normal"
                            id="date-picker-dialog"
                            label="Date Expiration"
                            format="MM/dd/yyyy"
                            value={selectedDate}
                            onChange={handleDateChange}
                            KeyboardButtonProps={{
                                'aria-label': 'change date',
                            }}
                            />
                        </Grid>
                    </MuiPickersUtilsProvider>
                    <div className="mb-3">
                        <label for="exampleInputPassword1" className="form-label">Resource de l'offre</label>
                        <input type="file" onChange={(e) => setFile(e.target.files[0])} className="form-control" id="exampleInputPassword1"/>
                    </div>
                    <Button type="submit" variant='contained' style={{color:"white", backgroundColor:"darkblue"}}>Sauvegarder</Button>
                </Paper>
                </form>
            </div>
        </>
    )
}
