import React, {useState, useEffect} from 'react';
import { useParams } from 'react-router-dom';
import { Paper } from '@material-ui/core';
import Sidebar from '../../../components/sidebar/Sidebar'
import TopBar from '../../../components/topBar/TopBar'
import axios from 'axios';
import './viewevent.css';
import { endpoint } from '../../../assets/appconst';

export default function ViewEvent() {
    let {id} = useParams();
    const [event, setEvent] = useState({})
    const getEvent = async () => {
       await axios.get(`${endpoint}/api/evenements/${id}`)
       .then((data)=>{
            setEvent(data.data)
       })
    }
    useEffect(() => {
        getEvent();
    }, [])
    return (
        <>
            <TopBar/>
            <div className='viewevent'>
                <div className='leftbar'>
                    <Sidebar/>
                </div>
                <div className='eventcorps'>
                    <div className='container'>
                        <Paper elevation={3} style={{padding:20}}>
                            <div class="card">
                            <div class="card-body">
                                <h5 class="card-title">{event.Titre}</h5>
                                <p class="card-text">{event.desc}</p>
                                <p class="card-text"><small class="text-muted">{new Date(event.dateEvenement).toLocaleString()}</small></p>
                            </div>
                            <img style={{height:350}} src={`${endpoint}/images/${event.image}`} class="card-img-bottom" alt={event.Titre}/>
                            </div>
                        </Paper>
                    </div>
                </div>
            </div>
        </>
    )
}
