import React, { useEffect, useState } from 'react';
import "./chart.css";
import { BarChart, Bar, XAxis, YAxis, Tooltip, CartesianGrid, ResponsiveContainer } from 'recharts';
import axios from "axios";
import { endpoint } from '../../assets/appconst';

const Chart = () => {
  const [graphData, setGraphData] = useState({})
    const getDataGraph = async()=>{
      await axios.get(`${endpoint}/api/user/graph`)
      .then((g)=>{
        setGraphData(g.data)
      })
    }
    useEffect(()=>{
      getDataGraph();
      console.log(graphData)
    }, []);
    const months = ["Janvier", "Février", "Mars", "Avril", "Mai", "Juin", "Juillet", "Août", "Septembre", "Octobre", "Novembre", "Décembre"];
    const getMonthNameByOrder = (order )=> months[order-1];
    return (
        <div className="chart">
            <h3 className="chartTitle">Utilisateurs en {new Date().getFullYear()}</h3>
            <ResponsiveContainer width="100%" aspect={4 / 1}>
              <BarChart data={graphData}>
                <XAxis dataKey="_id.mois" stroke="#304CA6" tickFormatter={getMonthNameByOrder} />
                <YAxis unit="U"/>
                <Tooltip labelFormatter={getMonthNameByOrder}/>
                <CartesianGrid stroke="black" strokeDasharray="3 3" vertical={false} />
                <Bar dataKey="count" fill="#304CA6" barSize={25} unit="user" name="Total"/>
              </BarChart>
            </ResponsiveContainer>
        </div>
    );
}

export default Chart;