import React, {useState, useEffect} from 'react';
import {Link} from 'react-router-dom';
import {Container} from 'react-bootstrap';
import { Button } from '@material-ui/core';
import MaterialTable from 'material-table'
import Sidebar from '../../components/sidebar/Sidebar';
import TopBar from '../../components/topBar/TopBar';
import { DeleteOutline, Edit, VisibilityOutlined, Add } from '@material-ui/icons';
import axios from 'axios';
import { endpoint } from '../../assets/appconst';
const Offres = () => {
    const [data, setData]=useState([])
    const deleteOffre = async (olData, resolve)=>{
     await axios.delete(`${endpoint}/api/offre/${olData._id}`)
     .then(res => {
      getAllOffres()
         resolve();
     })}
     const getAllOffres= async ()=>{
         const res= await axios.get(`${endpoint}/api/offre`)
         console.log(res.data)
         setData(res.data)
     }
     const editOffre = async (newRow, oldRow) => {
         await axios.put(`${endpoint}/api/offre/${oldRow._id}`, newRow)
         .then(res=>{
          getAllOffres()
         })
     }

     useEffect(() => {
      getAllOffres()
     }, [])

    return (
        <>
        <TopBar/>
        <div className="home">
            <div className="home-1">
                <Sidebar/>
            </div>
            <div className="home-2">
                    <div class="d-grid gap-2 d-md-block">
                        <div class="btn-group" role="group" >
                            <Link to="/insertoffre" style={{textDecoration: "none", color:"inherit"}}>
                                <Button type="button" className='btn' style={{
                                backgroundColor:"#304CA6",
                                color:"#FFF",
                                marginBottom: "20px"
                                }}
                                startIcon={<Add/>}
                                >Nouvelle Offre</Button>
                            </Link>
                            
                        </div>
                    </div>
                <Container>
                <MaterialTable
                    title="Offres"
                    columns={[
                        { title: 'Organisation', field: 'organisation' },
                        { title: 'Titre', field: 'Titre'},
                        { title: 'Description', field: 'desc' },
                        {
                        title: 'Date',
                        field: 'dateExpiration',
                        type: 'date'
                        },
                    ]}
                    data={data}
                    options={{
                        headerStyle: {
                          backgroundColor: '#304CA6',
                          color: '#FFF'
                        },
                        exportButton: true,
                        filtering: true,
                        grouping: true,
                        sorting: true,
                        actionsColumnIndex: -1,
                        pageSizeOptions: [2, 5, 10, 15, 20, 25, 50, 100, 500]
                      }}
                      editable={{
                        onRowDelete: (olData)=>
                              new Promise((resolve)=>
                              {
                                deleteOffre(olData, resolve)
                              } ),
                        onRowUpdate: (newRow, oldRow)=> new Promise((resolve, reject)=>{
                          editOffre(newRow, oldRow);
                          setTimeout(()=>resolve(), 500)
                        })
                      }}
                      actions={[
                        rowData=>(
                          {
                            icon:()=><Link to={`/viewoffre/${rowData._id}`} style={{textDecoration:"none", color:"black"}}><VisibilityOutlined/></Link>,
                            tooltip: 'view',
                            onClick: (rowData)
                          }
                        )
                      ]}
                    />
                </Container>
            </div>
    </div>
    </>
    );
}

export default Offres;
